import config from "@/script/config.js"
import store from "@/store/index.js"

module.exports = {
	/**
	 * 
	 * @param {Object} string []
	 */
	isEmptyString: function(string) {
		if (string == undefined ||
			typeof string == "undefined" ||
			!string ||
			string == null ||
			string == '' ||
			/^\s+$/gi.test(string)) {
			return true;
		} else {
			return false;
		}
	},
	// 
	sendLocal(conversition) {
		store.state.conversitionList.push(conversition);
		this.toBottom();
	},
	// 
	changeReaded(id) {
		let userConversition = store.state.conversitionList.filter(
			(x) =>
			x.SendId == id &&
			x.ReciverId == store.state.sender.Id &&
			!x.ReadFlag
		);
		if (userConversition.length > 0) {
			userConversition.map((x) => {
				x.ReadFlag = true;
			});
		}
		let query = {
			SendId: id,
			ReciverId: store.state.sender.Id,
		};
		store.state.socket.emit("changeMsgRead", query);
	},
	//
	toBottom(time) {
		setTimeout(() => {
			store.commit('setPropName', {
				propName: 'scrollTop',
				value: 999991
			})
			// this.scrollTop = 999991;
		}, time);
		setTimeout(() => {
			// this.scrollTop = 999999;
			store.commit('setPropName', {
				propName: 'scrollTop',
				value: 999999
			})
			clearTimeout();
		}, time + 100);
	},
	initSetInfo: function(data) {
		store.commit("setPropName", {
			propName: 'sender',
			value: data?.myInfo
		});
		uni.setStorageSync("token", data?.token);
		uni.setStorageSync("sender", JSON.stringify(data?.myInfo));
	},
	logout() {
		uni.removeStorageSync("token");
		uni.removeStorageSync("sender");
		store.state.socket.disconnect();
	},
	isLogin() {
		return uni.getStorageSync("token") &&  uni.getStorageSync("sender") ? true : false;
	},

	/**
	 * 
	 * @param {Object} phone []
	 */
	isPoneAvailable: function(phone) {
		var myreg = /^[1][0-9]{10}$/;
		if (!myreg.test(phone)) {
			return false;
		} else {
			return true;
		}
	},

	/**
	 * 
	 * @param {Object} sId []
	 */
	isCardID: function(sId) {
		var iSum = 0;
		var info = "";
		if (!/^\d{17}(\d|X)$/i.test(sId)) return "";
		sId = sId.replace(/X$/i, "a");
		let sBirthday = sId.substr(6, 4) + "-" + Number(sId.substr(10, 2)) + "-" + Number(sId.substr(12, 2));
		var d = new Date(sBirthday.replace(/-/g, "/"));
		if (sBirthday != (d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate())) return "";
		for (var i = 17; i >= 0; i--) iSum += (Math.pow(2, i) % 11) * parseInt(sId.charAt(17 - i), 11);
		if (iSum % 11 != 1) return "";
		return true;
	},
	/**
	 * 
	 */
	isLastDayOfMonth: function() {
		var flag = false;
		var date = new Date();

		var year = date.getFullYear();
		var month = date.getMonth() + 1;
		var today = 31;

		var new_year = year; //
		var new_month = month++; //
		if (month > 12) { //12
			new_month -= 12; //
			new_year++; //
		}
		var new_date = new Date(new_year, new_month, 1); //

		var month_last_day = (new Date(new_date.getTime() - 1000 * 60 * 60 * 24)).getDate();

		if (today == month_last_day) {
			flag = true;
		}
		return flag;
	},
	/**
	 * url
	 * @param {Object} name []
	 */
	getQueryString: function(name) {
		var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
		var r = window.location.search.substr(1).match(reg);
		if (r != null) {
			return unescape(r[2]);
		}
		return null;
	},

	//
	tipMsg: function(title, icon, time, mask) {
		title = title == undefined ? "" : title;
		icon = icon == undefined ? "none" : icon;
		time = time == undefined ? 1300 : time;
		mask = mask == undefined ? true : mask;
		uni.showToast({
			title: title,
			icon: icon,
			mask: mask,
			duration: time
		})
	},
	/** 
	 * 1.function
	 * @param {string} url 
	 * @param {Object} param 
	 * @description jsonparamJSON.parse(options.param)
	 */
	navigateTo(url, param = {}, flag) {
		if (store.state.isReadyLogin <= 0 && !flag) {
			this.loginTip();
		} else {
			let part = '';
			for (var item in param) {
				part += '&' + item + '=' + param[item];
			}
			url = "/pages" + url + part.replace('&', '?');
			uni.navigateTo({
				url: url,
				fail: err => {
					this.tipMsg('');
				},
			})
		}
	},

	/** 
	 * 2.function
	 * @param {string} url 
	 * @param {Object} param navigateTo
	 * @description navigateTo
	 */
	navigateBack(url, param = {}) {
		if (store.state.isReadyLogin <= 0) {
			this.loginTip();
		} else {
			let part = '';
			for (var item in param) {
				part += '&' + item + '=' + param[item];
			}
			url = "/pages" + url + part.replace('&', '?');
			uni.navigateBack({
				url: url,
				fail: err => {
					this.tipMsg('');
				},
			})
		}
	},

	// 3.switchTabswitchTab
	switchTab(url) {
		uni.switchTab({
			url: url
		})
	},
	// 4.redirectTonavigateTo
	redirectTo(url, param = {}, flag) {
		if (store.state.isReadyLogin <= 0 && !flag) {
			this.loginTip();
		} else {
			let part = '';
			for (var item in param) {
				part += '&' + item + '=' + param[item];
			}
			url = "/pages" + url + part.replace('&', '?');
			uni.redirectTo({
				url: url,
				fail(err) {
					console.log('redirectTo', err)
				},
			})
		}
	},
	// 5.reLaunchnavigateTo
	reLaunch(url, param = {}) {
		if (store.state.isReadyLogin <= 0) {
			this.loginTip();
		} else {
			let part = '';
			for (var item in param) {
				part += '&' + item + '=' + param[item];
			}
			url = "/pages" + url + part.replace('&', '?');
			uni.reLaunch({
				url: url,
				fail(err) {
					console.log('reLaunch', err)
				},
			})
		}
	},
	/**
	 * 6.function ,
	 * @param {String} appId ID
	 * @param {String} path 
	 * @param {Object}  extraData 
	 * @param {String}  envVersion 
	 */
	//appId (path  ?  App.onLaunchApp.onShow  Page.onLoad ,
	// extraData(object) ( App.onLaunchApp.onShow ),
	// envVersion 
	navigateToMiniProgram(appId, path, extraData, envVersion) {
		uni.navigateToMiniProgram({
			appId: appId,
			path: path,
			extraData: extraData,
			envVersion: envVersion,
			fail(err) {
				console.log('', err)
			}
		})
	},
	// 7.
	//extraData(object)  App.onShow 
	navigateBackMiniProgram(extraData) {
		uni.navigateBackMiniProgram({
			extraData: extraData,
			fail(err) {
				console.log('', err)
			}
		})
	},
	//HTML
	navigateToHtml(link, param = {}) {
		let part = '';
		for (var item in param) {
			part += '&' + item + '=' + param[item];
		}
		var url = "/pages/htmlShell/htmlShell?link=" + encodeURIComponent(link + part.replace('&', '?'));
		uni.navigateTo({
			url: url,
			fail: err => {
				this.tipMsg('');
			},
		})
	},
	//
	getTelephoneInfo(){
		return new Promise((resolve, reject) => {
			var data = uni.getStorageSync("telephoneInfo");
			if(!data){
				// 

				let btn = wx.getMenuButtonBoundingClientRect();

				uni.getSystemInfo({
					success: e => {
						let info = {
							screenHeight:e.screenHeight,
							statusBarHeight:e.statusBarHeight,
							windowWidth:e.windowWidth,

							top:btn.top,

						}
						uni.setStorageSync("telephoneInfo",info);
						resolve(info);
					},
					fail: (err) => {
						reject(err);
					}
				})
				
			}else{
				resolve(data);
			}
		})
	},
}
